@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
/*-------------------- MAIN --------------------*/

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}
.navbar {
  background: #000;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 80px;
}

.container {
  z-index: 1;
  width: 100%;
  max-width: 1920px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 50px;
  padding-left: 50px;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.navbar-icon {
  margin-right: 0.5rem;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  text-align: center;
  justify-content: end;
}

.nav-item {
  height: 80px;
  border-bottom: 2px solid transparent;
}

.nav-item:hover {
  border-bottom: 2px solid #fbff00;
}

.nav-links {
  color: #f3f3f3;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

/*-------------------- HOME --------------------*/

.home-banner {
  height: 100vh;
  width: 100%;
  background-color: #000;
}

.home-banner-text-wrapper {
  position: absolute;
  top: 50%;
  display: block;
  width: 100%;
  transform: translateY(-50%);
  text-align: center;
  color: #fff;
}

.home-banner-title {
  opacity: 1;
  padding: 10px;
  font-size: 48px;
  letter-spacing: 15px;
  font-weight: 300;
  margin-bottom: 20px;
  background: #000;
  position: relative;
  -webkit-animation: text 3s 1;
          animation: text 3s 1;
}

.home-banner-subtitle {
  opacity: 1;
  margin: 0;
  padding: 0;
  font-size: 24px;
  letter-spacing: 5px;
  font-weight: 100;
}

/*-------------------- FOOTER --------------------*/

.footer {
  color: #f3f3f3;
  background-color: #000;
  font-family: 'Lato', sans-serif;
  font-size: 12px;
  letter-spacing: 2px;
  padding-top: 3em;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 1em;
  position: relative;
  width: 100%;
}

.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.content-wrap {
  flex: 1 1;
}

/*-------------------- ANIMATIONS --------------------*/

@-webkit-keyframes text {
  0% {
    color: black;
    margin-bottom: -40px;
  }
  30% {
    letter-spacing: 20px;
    margin-bottom: -40px;
  }
  60% {
    letter-spacing: 15px;
    margin-bottom: 20px;
  }
}

@keyframes text {
  0% {
    color: black;
    margin-bottom: -40px;
  }
  30% {
    letter-spacing: 20px;
    margin-bottom: -40px;
  }
  60% {
    letter-spacing: 15px;
    margin-bottom: 20px;
  }
}

/*-------------------- MEDIA --------------------*/

@media screen and (max-width: 960px) {
  .home-banner-title {
    font-size: 5vw;
  }

  .home-banner-subtitle {
    font-size: 2.5vw;
  }
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #000;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    color: #fbff00;
    transform: scale(1.2);
    transition: all 0.3s ease;
  }

  .nav-item:hover {
    border: none;
  }

  .menu-icon {
    color: #f3f3f3;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .nav-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 120px;
  }
}

@media screen and (max-width: 500px) {
  .home-banner-title {
    font-size: 7vw;
  }

  .home-banner-subtitle {
    font-size: 5vw;
  }
}

